import React, { useState } from 'react'
import Header from '../Header'
import Footer from '../Footer'
import styles from '../styles/Create_equip.module.css'
import { Button } from '@mui/material'
import { Link, useNavigate } from 'react-router-dom'

const now = new Date()
const year = now.getFullYear()
function Create_equip() {
  const [ActiveFood, SetActiveFood] = useState(2)
  const [EmergencyFood, SetEmergencyFood] = useState(1)
  const [SpareFood, SetSpareFood] = useState(0)
  const [Water, SetWater] = useState(1)
  const [Rice, SetRice] = useState(1)
  const [ClimbingType, SetClimbingType] = useState("")
  const [ClimbingId, SetClimbingId] = useState(0)
  const [ClimbingName, SetClimbingName] = useState("")
  const [ClimbingYear, SetClimbingYear] = useState(year)
  const [AdditionalEquip, SetAdditionalEquip] = useState("")
  const navigate = useNavigate()
  const JumpToNext = () => {
    navigate("/Create/SelectEquip", {state: {
      activefood: ActiveFood,
      emergencyfood: EmergencyFood,
      sparefood: SpareFood,
      water: Water,
      rice: Rice,
      climbingtype: ClimbingType,
      climbingid: ClimbingId,
      climbingname: ClimbingName,
      climbingyear: ClimbingYear,
      additionalequip: AdditionalEquip,
    }})
  }
  const ForShortClimbing = (type) => {
    SetClimbingType(type)
    if (type.endsWith("")) {
      SetActiveFood(1)
      SetEmergencyFood(1)
      SetWater(1)
      SetRice(0)
    } else {
      SetActiveFood(2)
      SetEmergencyFood(1)
      SetWater(2)
      SetRice(1)
    }
  }
  return (
    <div className={styles.Home}>
      <Header></Header>
      <main className={styles.main}>
        <h1 className={styles.title}>E</h1>
        <div>

          <table border="1" className={styles.table}>
            <tbody>
              <tr>
                <td></td>
                <td>twv<input type='number' value={year} className={styles.NumberInput} onClick={(e) => { SetClimbingYear(e.target.value) }}></input>-
                  <input type="number" className={styles.NumberInput} onChange={(e) => { SetClimbingId(e.target.value) }}></input></td>
              </tr>
              <tr>
                <td></td>
                <input type='text' onChange={(e) => { SetClimbingName(e.target.value) }}></input>
              </tr>
              <tr>
                <td></td>
                <td>
                  <select name="ClimbingType" onChange={(e) => { ForShortClimbing(e.target.value) }}>
                    <option value="" ></option>
                    <option value="" ></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value="" ></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                  </select>
                </td>
              </tr>
              <tr>
                <td></td>
                <td><input type="number" value={Water} className={styles.NumberInput} onClick={(e) => { SetWater(e.target.value) }}></input></td>
              </tr>
              <tr>
                <td></td>
                <td><input type='number' value={ActiveFood} className={styles.NumberInput} onClick={(e) => { SetActiveFood(e.target.value) }}></input></td>
              </tr>
              <tr>
                <td></td>
                <td><input type='number' value={EmergencyFood} className={styles.NumberInput} onClick={(e) => { SetEmergencyFood(e.target.value) }}></input></td>
              </tr>
              <tr>
                <td></td>
                <td><input type='number' value={SpareFood} className={styles.NumberInput} onClick={(e) => { SetSpareFood(e.target.value) }}></input></td>
              </tr>
              <tr>
                <td></td>
                <td><input type='number' value={Rice} className={styles.NumberInput} onClick={(e) => { SetRice(e.target.value) }} ></input></td>
              </tr>
              <tr>
                <td></td>
                <td><textarea rows="5" cols="33" value={""} onClick={(e) => { SetAdditionalEquip(e.target.value) }}></textarea></td>
              </tr>
            </tbody>
          </table>
          <button className={styles.NextBotton} onClick={() => {JumpToNext()}}  >
            
          </button>
        </div>
      </main >
      <Footer></Footer>
    </div >
  )
}

export default Create_equip